'use strict';

(function () {
  let slider = document.querySelector('.countries-information__slide-list');
  let control = document.querySelector('.countries-information__control-list');
  let controlSlide = document.querySelector('.countries-information__slide-name');

  const DESKTOP_WIDTH = 1213;
  const debouceTime = 700;

  slider.addEventListener('touchstart', function () {
    let onMove = function () {

    };
    if (window.innerWidth < DESKTOP_WIDTH) {
      let moveTitle = function () {
        let activeSlide = document.querySelector('.swiper-slide-active');
        control.style.transform = `translateX(${slider.getBoundingClientRect().left * (controlSlide.getBoundingClientRect().width / slider.getBoundingClientRect().width)}px)`;
        if (slider.getBoundingClientRect().left > 0) {
          control.style.transform = `translateX(0)`;
        }
        document.querySelector('.countries-information__slide-name--active').classList.remove('countries-information__slide-name--active');
        document.querySelector(`[data-name='${activeSlide.dataset.slide}']`).classList.add('countries-information__slide-name--active');
        slider.removeEventListener('touchend', onUp);
      }

      moveTitle = window.util.debounce(moveTitle, debouceTime);

      let onUp = function () {
        moveTitle();
        slider.removeEventListener('touchmove', onMove);

      };
      slider.addEventListener('touchmove', onMove);
      slider.addEventListener('touchend', onUp);
    }
  })

  let onControlClick = function (evt) {
    if (evt.target.hasAttribute('data-name')) {
      let slideName = evt.target;
      let activeSlideName = control.querySelector('.countries-information__slide-name--active');
      let slide = document.querySelector(`[data-slide='${evt.target.dataset.name}']`);
      let activeSlide = document.querySelector('.countries-information__slide-item--active');
      changeSlide(slideName, activeSlideName, slide, activeSlide);
  }
}

  let changeSlide = function (slideName, activeSlideName, slide, activeSlide) {
    activeSlideName.classList.remove('countries-information__slide-name--active');
    slideName.classList.add('countries-information__slide-name--active');
      if (window.innerWidth > DESKTOP_WIDTH) {
        activeSlide.style = 'transition: opacity 2s, 0s transform 2s'
        activeSlide.classList.remove('countries-information__slide-item--active');
        slide.classList.add('countries-information__slide-item--active');
        slide.style = `transform: translateX(-${slide.offsetLeft}px);`
      } else {
        activeSlide.classList.remove('countries-information__slide-item--active');
        slide.classList.add('countries-information__slide-item--active');
        slider.style = `transform: translate3d(${-slide.offsetLeft}px, 0px, 0px);`
        control.style.transform = `translateX(${slider.getBoundingClientRect().left * (controlSlide.getBoundingClientRect().width / slider.getBoundingClientRect().width)}px)`;
      }
    };

  control.addEventListener('click', onControlClick);

  window.fullSlider = {
    changeSlide: changeSlide
  }
})()
